.tile
{
  width: 100px;
  height: 150px;
  border: 3px solid;
  border-radius: 10px;
  background-color: rgb(255,234,187);
}

.tile img
{
    object-fit: cover;
}

.tile:hover
{
  border-color: rgb(229,203,11);
}

.tile:active
{
  transform: rotate(0deg);
}

.face-down
{
  background-color: rgb(36, 89, 28);
  
}

.queue
{
  transform: rotate(270deg);
}

.grid-item
{
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

.player-grid-1
{
  display: grid;
  grid-template-columns: repeat(9,1fr);
  grid-template-rows: repeat(4,1fr);
  grid-gap: 10px;
  padding: 10px;
}

.player-grid-2
{
  display: grid;
  grid-template-columns: repeat(9,1fr);
  grid-template-rows: repeat(4,1fr);
  grid-gap: 10px;
  padding: 10px;
  transform: rotate(180deg);
}

.Control
{
  position: absolute;
  right: 0;
  padding: 10px;
}

